.shimmer-card {
  text-align: center;
  background-color: #bbb;
  border: 2px solid #d9cdcd;
  border-radius: 10%;
  width: 280px;
  height: 200px;
  padding: 6px;
  transition: all .3s ease-out;
  position: relative;
  overflow: hidden;
}

.shimmer-card:before {
  content: "";
